//base/_forms.scss

fieldset {
  background-color: lighten($base-border-color, 10%);
  border: $base-border;
  margin: 0 0 $small-spacing;
  padding: $base-spacing;
}


input,
label,
select {
  color: $dark-gray;
  display: inline-block;
  font-family: $base-font-family;
  font-size: $base-font-size;
}


label {
  margin-bottom: $small-spacing / 3;

  &.required::after {
    content: "*";
  }

  abbr {
    display: none;
  }
}

#{$all-text-inputs},
select[multiple=multiple],
textarea {
  background-color: $base-background-color;
  border: $base-border;
  border-radius: $base-border-radius;
  box-shadow: $form-box-shadow;
  box-sizing: border-box;
  font-family: $base-font-family;
  font-size: $base-font-size;
  margin-bottom: $base-spacing / 2;
  padding: $base-spacing/2;
  transition: border-color;
  width: 100%;

  &:hover {
    border-color: darken($base-border-color, 10%);
  }

  &:focus {
    border-color: $purple;
    box-shadow: $form-box-shadow-focus;
    outline: none;
  }
}

textarea {
  resize: vertical;
}

input[type="search"] {
  @include appearance(none);
}

input[type="checkbox"],
input[type="radio"] {
  display: inline;
  margin-right: $small-spacing / 2;
}

input[type="file"] {
  padding-bottom: $small-spacing;
  width: 100%;
}

input[type="submit"].button {
  color: $white;
}

select {
  margin-bottom: $base-spacing;
  max-width: 100%;
  width: auto;
}

.indented-inputs {
  padding-left: 2em;
}

.teal-label {
  color: $teal;
  > label {
    @extend .teal-label;
  }
}

.label {
  &-bold {
    font-weight: 600;
  }
  &-alert, &-error {
    color: $alert;
    display: block;
    font-size: em(18);
  }
  &-inline-normal {
    margin-bottom: .8em;
  }
}

form .error {
  margin-top: -.3em;
}

.form {
 &-attention {
  margin-bottom: $base-spacing;
  > label{
    color: $alert;
    display: block;
    font-size: em(18);
    margin-bottom: $small-spacing;
    }
  }
 &-medium-width {
   max-width: em(650);
 }

 &-narrow {
   max-width: em(550);
 }
}

.form-group {
  display: inline-block;
  margin-bottom: 1.5em;
  vertical-align: middle;
  width: 100%;
  > input {
    margin-bottom: 0;
    width: 74%;
    @include breakpoint($small-screen-down) {
      width: 100%;
    }
  }

  > .label-inline {
      margin-bottom: 0;
      width: 25%;
      @include breakpoint($small-screen-up) {
        padding-right: 1em;
        text-align: right;
      }
      @include breakpoint($small-screen-down) {
      width: 100%;
    }
  }

  > .label-inline-align-left {
      margin-bottom: 0;
      width: 20%;
      @include breakpoint($small-screen-up) {
        padding-right: 1em;
      }
      @include breakpoint($small-screen-down) {
      width: 100%;
    }
    + input {
      width: 79%;
      @include breakpoint($small-screen-down) {
      width: 100%;
      }
    }
  }

  > .input-aligned {
     margin-left: 20.5%;
     //margin-top: 1.5em;
     @include breakpoint($small-screen-down) {
     margin-left: 0;
     //float: right;
    }
     @include breakpoint($extra-small-screen-down) {
     margin-left: 0;
     width: 100%;
    }
    > .button {
      margin-top: 1.5em;
    }

  }

  &-narrow {
    padding-right: 20%;
      @include breakpoint($small-screen-down) {
        display: block;
        float: none;
        margin: 0 auto;
        padding-right: 0;
        width: 76%;
     }
     @include breakpoint($extra-small-screen-down) {
       width: 100%;
     }
  }
   .error {
  //  @extend .button-input-aligned;
      margin-top: .5em;
  }
}
