//modules/_expander.scss
//Show and hide content with this module

.expander {
  @include clearfix;
  $base-font-size: 1em !default;
  $base-line-height: 1.5em !default;
  $base-spacing: 1.5em !default;
  $action-color: #477DCA !default;
  $base-link-color: $action-color !default;
  $dark-gray: #333 !default;
  $light-gray: #DDD !default;
  $base-font-color: $dark-gray !default;
  $expander-arrow-width: 0.7em;
  $expander-toggle-size: 1em;
  $expander-toggle-arrow-size: $expander-toggle-size;
  $expander-toggle-margin: 1em;
  margin-bottom: 2em;

  &-half {
    float: left;
    padding-right: 2em;
    width: 50%;
    @include breakpoint($large-screen-down) {
      width: 100%;
    }
  }

  .expander-trigger {
    @include user-select(none);
    border-bottom: 1px solid $light-gray;
    color: $base-link-color;
    cursor: pointer;
    display: block;
    font-size: $expander-toggle-size;
    margin-bottom: $expander-toggle-size;
    padding-left: $expander-toggle-size * 2;
    padding-bottom: $expander-toggle-size / 4;
    position: relative;
    text-decoration: none;
      &:before {
        @include retina-image(arrow-forward-orange, 8px 12px);
        content: "";
        position: absolute;
        top: 1px;
        left: 6px;
        right: 0;
        bottom: 0;
        width: 11px;
        -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        transform: rotate(90deg);
      }
      &.expander-hidden:before {
        //@include retina-image(arrow-forward-orange, 12px 8px);
        //content: "";
      //  position: absolute;
        top: 5px;
        left: 10px;
      //  right: 0;
        //bottom: 0;
        width: 12px;
        -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        transform: rotate(0deg);
      }
  }

  .expander-content p {
    color: $base-font-color;
    line-height: $base-line-height;
  }

  .expander-hidden + .expander-content {
    display: none;
  }
}
