//Login Pages

.login-header {
  @include clearfix;
  background: $white;
  padding: 2em 1em .1em;
  @breakpoint (max-height: 800px), (max-width: $large-screen-down) {
    padding: $base-spacing .5em 0;
  }
  @breakpoint (max-width: $small-screen-down) {
    padding: .5em .5em 0;
  }
   > .center {
    @breakpoint (max-height: 800px) {
      margin-bottom: $small-spacing/2;
    }
    @breakpoint (max-width: $small-screen-down) {
     font-size: 1.55em;
      }
  }
}

.branding-logo {
  height: 47px;
  margin-bottom: $small-spacing;
  width: 265px;
  @breakpoint (max-width: $medium-screen-down), (max-height:800px) {
    height: 39px;
    margin-bottom: $small-spacing/2;
    width: 220px;
  }
  @include breakpoint ($small-screen-down) {
    height: 34px;
    margin-bottom: 0;
    width: 190px;
  }
}

