.branding-center {
  //@include reset-display;
  @include span(6);
  @include push(3);
    @include breakpoint($medium-screen-down) {
      @include span(4);
      @include push(0);
      padding: .5em 0;
      }
}

.inner-banner > .container {
  @include breakpoint($medium-screen-down) {
    padding: 0
  }
}
