// Typography
$base-font-family: Arial, Helvetica, "Lucida Grande", Tahoma, Verdana, sans-serif;
$heading-font-family: $base-font-family;
$bold: 600;

// Font Sizes
$base-font-size: 100%;

// Line height
$base-line-height: 1.5;
$heading-line-height: 1.3;

// Other Sizes
$base-border-radius: 4px;
$base-spacing: $base-line-height * 1em;
$small-spacing: $base-spacing / 2.25;
$base-z-index: 0;

// Colors
//Avoid using the color variables directly in partials--
//Apply to other variables instead

$white: #fff;
$white-gray: #F9F9F9;
$light-gray: #e3e3e3;
//$light-gray: #ddd;
$medium-gray: #999;
$dark-gray: #717171;
$footer-gray: #ACB5C0;
$orange: #C85300;
$purple: #5A5EA9;
//$dark-purple: #2E338C;
$light-teal:#E9F0EF;
//$teal: #40806A;
$teal: #4A7B71;
$black: #282828;
$alert: #B14F4E;
$error: #D0021B;
$error-background: #FCE9E7;
$alert-background: $alert;
$success: #4b850c;

// Font Colors
$base-background-color: #fff;
$base-font-color: darken($dark-gray, 2%);
$base-heading-color: $purple;
$action-color: $purple;

// Background-colors
$background-light-gray: darken($base-background-color, 4%);
// Border
$base-border-color: $light-gray;
$base-table-color: $white;
//$base-table-hover: $white-gray;
$base-border: 1px solid $base-border-color;
$base-border-darker: 1px solid darken($base-border-color, 6%);

// Forms
$form-box-shadow: inset 0 1px 3px rgba(#000, 0.06);
$form-box-shadow-focus: $form-box-shadow, 0 0 5px adjust-color($purple, $lightness: -5%, $alpha: -0.3);

