//base/_typography.scss

body {
  @include font-feature-settings("kern", "liga", "pnum");
  -webkit-font-smoothing: antialiased;
  color: $base-font-color;
  font-family: $base-font-family;
  //font-size: $base-font-size;
  line-height: $base-line-height;
}

// headings
h1,
h2,
h3,
h4,
h5,
h6 {
  color: $base-heading-color;
  font-family: $heading-font-family;
  font-weight: $bold;
  line-height: $heading-line-height;
  margin: 0 0 $small-spacing;
}

h1, .h1 {
  font-size: em(36);
}

h2, .h2 {
  font-size: em(28);
}

h3, .h3 {
  font-size: em(28);
}

h4, .h4 {
  font-size: em(22);
}

h5, .h5 {
  font-size: em(20);
}

h6, .h6 {
  font-size: em(18);
}

p {
  margin: 0 0 $base-spacing/2;
}

// Links
// Default Link
a {
  color: $action-color;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.1s linear;

  &:active,
  &:focus,
  &:hover {
    color: tint($action-color, 20%);
  }
}

// link with body font color
.link-base-color {
  color: $base-font-color;
}

// End Links

.block-span {
  display: block;
}

.heading-inline {
  display: inline;
}


@mixin text-larger($color: inherit, $font-size: em(18)) {
  color: $color;
  font-size: $font-size;
}

.text-larger {
  @include text-larger();
  &-teal {
  @include text-larger($teal);
  }
  &-purple {
  @include text-larger($base-heading-color);
  }
}

.page-sm-heading-teal {
  @include text-larger($teal, em(22));
  //margin-bottom: $base-spacing;
}

.page-lg-heading {
  color: $base-heading-color;
  display: inline-block;
  font-size: em(28);
  font-weight: $bold;
  margin-bottom: .2em;
  @include breakpoint($small-phone-screen-down) {
    display: block;
  }
}

.text-larger-margin {
  margin-bottom: $base-spacing;
}

.teal {
  color: $teal;
}

.text-purple {
  color: $purple;
}

.normal {
  font-weight: normal;
}

.error, .alert, .attention {
  color: $error;
}

.success {
  color: $success;
}

.dashboard-grid > h3 {
  font-size: em(20);
  color: $teal;
   .smaller {
     font-size: modular-scale(-1);
     font-weight: normal;
   }
}

.asterick {
  text-align: right;
  font-size: modular-scale(-1);
  margin-bottom: .2em;
  padding-right: 1.5em;
}

.smaller {
  font-size: em(14);
}

.indented {
  margin-left: $base-spacing;
  &-wide {
    margin-left: $base-spacing*1.5;
  }
}

.white-text {
  color: $white;
}

.tag-line {
  color: $base-heading-color;
  font-style: italic;
  font-size: em(14);
}

.m-center {
  @include breakpoint($small-screen-down) {
    text-align: center;
  }
}
