// modules/_footer.scss

//footer logged out
.help-link:before {
  @include retina-image(info-icon, 18px 18px);
  content:" ";
  display: inline-block;
  height: 18px;
  left: 1em;
  margin: 0 .4em 0 .8em;
  vertical-align: text-bottom;
  width: 18px;
}

//footer on logged in pages

.footer-logged-in {
  background-color: $footer-gray;
  color: $black;
}

.footer-grid {
  //@include container();
  padding-bottom: 15px;
  padding-top: 25px;
  a {
    color: $black;
    display: inline-block;
    font-size: em(14);
    margin-left: 3em;
    margin-bottom: 10px;
  //  -webkit-transition: .4s linear;
  //  transition: .4s linear;
    @include breakpoint($medium-screen-down) {
      margin-left: 0;
    }
    &:hover, &:focus {
      margin-bottom: 9px;
      border-bottom: 1px solid $black;
      //text-decoration: underline;
    }
  }
  div {
   @include span(3);
   @include breakpoint($small-screen-down) {
     @include span(6);
     @include omega(2n);
   }
   @include breakpoint($extra-small-screen-down) {
     @include span(12);
     text-align: center;
   }
  }
}

.footer-copyright {
  border-top: 1px solid $white;
  font-size: em(14);
}
