//base/_tables.scss

table {
  @include font-feature-settings("kern", "liga", "tnum");
  background-color: $base-table-color;
  border-collapse: collapse;
  margin: $small-spacing 0;
  table-layout: fixed;
  width: 100%;
}

th {
  border-bottom: 1px solid $base-border-color;
  color: $teal;
  font-weight: normal;
  padding: 0;
  text-align: left;
}

th:first-child {
  padding-left: 1.5em;
}

th:last-child {
  padding-right: 1.5em;
}

td {
  border-bottom: $base-border;
  padding: $small-spacing 0;
}

tr,
td,
th {
  vertical-align: middle;
}

//Reporting Dashboard
%invisible-header {
  border-bottom: 0;
  padding: 0;
}

.table-names {
  width: 50%;
}

.table-data-single {
  width: 25%;
  text-align: right;
}

.centered-th {
  padding-right: 1.5em;
  text-align: center;
}

.table-data-double {
  width: 35%;
  text-align: right;
}

.table-icon {
  width: 10%;
}

table td img {
  margin-left: .5em;
  vertical-align: text-bottom;
}

tbody {
  .table-header:hover {
    background-color: inherit;
  }
  tr:hover {
    background-color: darken($base-table-color, 3%);
  }

}
