// modules/_tabs.scss

// These tabs are horizontal at larger breakpoints
// and stack at smaller breakpoints

.accordion-tabs-minimal {
  $base-border-color: $white-gray !default;
  $base-link-color: $action-color !default;
  $dark-gray: #333 !default;
  $medium-screen: $medium-screen-up;
  $tab-link-color: $teal;
  $tab-border: $base-border;
  $tab-border-radius: $base-border-radius;
  $tab-active-background: $base-border-color;
  $tab-inactive-color: $base-background-color;
  $tab-inactive-hover-color: darken($base-background-color, 3%);
  $tab-mode: $large-screen-up;

  @include clearfix;
  line-height: 1.5;
  padding: 0;

  &--students {
    position: relative;
  }


  li.tab-header-and-content {
    list-style: none;

    @include breakpoint($tab-mode) {
      display: inline;
    }
  }

  a.tab-link {
    background: $white-gray;
    border: $tab-border;
    //border-top: $tab-border;
    border-radius: $tab-border-radius;
    color: $tab-link-color;
    display: block;
    font-weight: 600;
  //  margin: ($base-spacing / 1) ($gutter) 0 0 ;
    margin-top: .5em;
    padding: 0.75em 1.2em;
    text-decoration: none;

    @include breakpoint($tab-mode) {
      @include inline-block;
      background: none;
      border: none;
    }

    &:hover {
      color: $base-link-color;
    }

    &:focus {
      outline: none;
    }

    &.is-active {
      background: $tab-active-background;
      color: $purple;
      //border-bottom: 5px solid $purple;
      border: $tab-border;
      border-bottom: $tab-border;
      border-radius: $tab-border-radius;
      margin-bottom: 0;

      @include breakpoint($tab-mode) {
      //  border-bottom: 4px solid $purple;
        background: none;
        border-bottom: 1px solid #fff;
        border-radius: $tab-border-radius $tab-border-radius 0 0;
        margin-bottom: -1px;
      }
    }
  }

  .tab-content {
    border-top: 0;
    display: none;
    padding: 1.2em ;
    width: 100%;

    @include breakpoint($tab-mode) {
      border-top: $tab-border;
      float: left;
    }
  }
}
