//modules/_modal.scss

.modal {
  $base-border-radius: 3px !default;
  $base-background-color: white !default;
  $base-font-size: 1em !default;
  $base-line-height: 1.4em !default;
  $action-color: #477DCA !default;
  $dark-gray: #333 !default;
  $light-gray: #DDD !default;
  $base-font-color: $dark-gray !default;
  $modal-padding: 3em;
  $modal-background: $base-background-color;
  $modal-close-color: $white;
  $modal-image-height: 135px;
  $modal-image-width: $modal-image-height;
  $modal-trigger-image-width: 300px;

  label {
    cursor: pointer;
    margin-bottom: 0;
  }

  label img {
    border-radius: $modal-trigger-image-width / 2;
    display: block;
    max-width: $modal-trigger-image-width;
  }

  .modal-state {
    //display: none;
  }

  .modal-trigger {
    @include button(flat, $action-color);
    padding: 0.8em 1em;
  }

  .modal-fade-screen { // overlay
    @include transition(opacity 0.25s ease);
    @include position(fixed, 0px 0px 0px 0px);
    background: rgba(172,181,192,0.70);
    opacity: 1;
    padding-top: 0.6em;
    text-align: left;
    //visibility: hidden;
    z-index: 99999999999;

    @include breakpoint($large-screen-down) {
      padding-top: 10em;
    }

    .modal-bg {
      @include position(absolute, 0px 0px 0px 0px);
      cursor: pointer;
    }
  }

  .modal-top {
    @include clearfix;
    background: #666BC1;
    height: 2.5em;
  }

  .modal-close {
    @include position(absolute, ($modal-padding / 5) ($modal-padding / 5) null null);
    @include size(1.5em);
    background: #666BC1;
    cursor: pointer;

    &:after,
    &:before {
      @include position(absolute, 4px 3px 0 50%);
      @include transform(rotate(45deg));
      @include size(0.1em 1.2em);
      background: $modal-close-color;
      content: '';
      display: block;
      margin: -3px 0 0 -1px;
    }

    &:hover:after,
    &:hover:before {
      background: darken($modal-close-color, 10%);
    }

    &:before {
      @include transform(rotate(-45deg));
    }
  }

  .modal-inner {
    @include transition(opacity 0.25s ease);
    background: $purple;
    border-radius: $base-border-radius;
    margin-top: 0;
    margin: auto;
    height: 28%;
    min-height: 20em;
    overflow: auto;
    padding: 0;
    position: absolute;
    max-width: em(665);
    width: 95%;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;

    @include breakpoint($small-phone-screen-down) {
      height: 45%;
    }


    h2 {
      color: $white;
      text-align: left;
      @include breakpoint($small-screen-down) {
        font-size: 1em;
      }
    }

    p {
      color: $white;
      line-height: $base-line-height;
    }

    .modal-intro {
      font-weight: 800;
    }

    .modal-content {
      color: $white;
      padding: ($modal-padding / 2) $modal-padding;
      @include breakpoint($small-screen-down) {
       padding: ($modal-padding / 3);
      }
      .small-label {
        color: $light-gray;
        font-size: .8em;
        font-weight: normal;
        margin-top : 1em;
      }
    }



    .modal-double-buttons {
      position: absolute;
      bottom: 1em;
      right: 4em;
      left: 4em;
        @include breakpoint($small-phone-screen-down) {
          right: 1em;
          left: 1em;
      }
      > button {
        float: left;
        width: 47%;
        @include breakpoint($small-phone-screen-down) {
          padding: 1em .5em;
        }
        &:first-child {
          margin-right: 6%;
        }
      }
    }

    a.cta {
      color: white;
      display: inline-block;
      margin-right: 0.5em;
      margin-top: 1em;

      &:last-child {
        padding: 0 2em;
      }
    }
  }

  .modal-state:checked + .modal-fade-screen {
    opacity: 1;
    visibility: visible;
  }

  .modal-state:checked + .modal-fade-screen .modal-inner {
    top: 0.5em;
  }
}

.modal-open {
  overflow: hidden;
}

