//base/_buttons.scss

//#{$all-button-inputs} {
  .button {
  @include appearance(none);
  -webkit-font-smoothing: antialiased;
  background-color: $action-color;
  border-radius: $base-border-radius;
  border: none;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: $base-font-family;
  font-size: $base-font-size;
  font-weight: 600;
  line-height: 1;
  padding: .8em 1em;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;

  &:hover,
  &:focus {
    background-color: tint($action-color, 14%);
    color: #fff;
  }

  &:disabled {
    cursor: not-allowed;
    background-color: tint($action-color, 55%);
  }

  &.button-x-large {
    font-size: 1.6em;
    padding: .7em 4.6em;
  }

  &.button-large {
    font-size: em(20);
  }

  &.button-centered {
    display: block;
    margin: 0 auto;
    text-align: center;
  }

  &.button--red {
    background: $alert;
  }

  &.button-mobile-block {
    @include breakpoint($small-phone-screen-down) {
    display: block;
    margin-left: 0;
    margin-top: $base-spacing*1.5;
   }
  }
}

.close, .unstyled-button {
  @include appearance(none);
  padding: 0;
  cursor: pointer;
  background: 0 0;
  border: 0;
}

.button-link {
  @extend .unstyled-button;
  color: $action-color;
  padding: 1px 0; //otherwise bottom of font below baseline getting cut off on Safari and Firefox
  font-weight: normal;
  &:active,
  &:focus,
  &:hover {
    color: tint($action-color, 20%);
  }
}



.mobile-button {
    @include breakpoint($small-screen-down) {
    @include appearance(none);
    -webkit-font-smoothing: antialiased;
    border-radius: $base-border-radius;
    border: none;
    cursor: pointer;
    line-height: 1;
    padding: .5em;
    text-decoration: none;
    user-select: none;
    vertical-align: middle;
    white-space: nowrap;
    color: $action-color;
    display: block;
    border: 2px solid $action-color;
   }
}
