//base/_lists.scss

ul,
ol {
  list-style-type: none;
  margin: 0;
  padding: 0;

  &%default-ul {
    list-style-type: disc;
    margin-bottom: $small-spacing;
    padding-left: $base-spacing;
  }

  &%default-ol {
    list-style-type: decimal;
    margin-bottom: $small-spacing;
    padding-left: $base-spacing;
  }
}

dl {
  margin-bottom: $small-spacing;

  dt {
    font-weight: bold;
    margin-top: $small-spacing;
  }

  dd {
    margin: 0;
  }
}


//Adds back in disc style when needed
.bullet {
  list-style-type: disc;
  list-style-position: inside;
  @include breakpoint($small-screen-down) {
  text-align: left;
 }
  > li {
    margin-bottom: .3em;
  }
  &-indented {
    padding-left: 1em;
  }
  &-heading {
    margin-bottom: .3em;
  }
}
