//layouts/_columns.scss

.student-list {
  @include columns(4 10px);
    @include breakpoint($small-screen-down) {
      @include columns(3 10px);
    }
    @include breakpoint($small-phone-screen-down) {
      @include columns(2 10px);
    }
}

.student-name {
  @include breakpoint($small-screen-down) {
  margin-bottom: 1em;
  }
}
