//modules/_navigation.scss

.nav-items {
  color: $white;
  font-size: em(20);
  font-weight: $bold;
  text-align: center;
  > .nav-icon {
  display: inline-block;
  margin: .4em 5em;
  @include breakpoint($large-screen-down) {
    margin: .4em 3em;
  }
  @include breakpoint($medium-screen-down) {
    margin: .4em 1.5em;
  }
   @include breakpoint($small-phone-screen-down) {
     margin: .4em 0;
     width: 100%;
  }
  > a {
    color: $white;
  }
    &:before {
      @include retina-image(link-icon, 17px 17px);
      height: 17px;
      width: 17px;
      content: " ";
      display: inline-block;
      position: relative;
      left: -0.5em;
      top: 0.1em;
    }
  }
}
