// modules/_alert.scss
.alert {
  border-radius: $base-border-radius;
  margin-bottom: $small-spacing;
  padding: .8em $base-spacing;

  &-dismissible {
    padding-right: 2em;
    .close {
      color: inherit;
      float: right;
      font-size: $base-font-size *1.5;
      font-weight: 600;
      line-height: 1;
      opacity: .7;
      position: relative;
      right: -0.75em;
      top: 4px;
      &-red {
        @include retina-image(close-icon-red, 14px 14px);
        height: 14px;
        width: 14px
      }
      &-white {
        @include retina-image(close-icon-white, 17px 17px);
        height: 17px;
        width: 17px
       }
      &:hover, &focus{
        opacity: 1;
      }
    }
  }
  &-error {
    background-color: $error-background;
    color: $error;
  }
  &-site-wide {
    background-color: $alert;
    border-radius: 0;
    color: $white;
  }
}
