//modules/kaplan-consultants-profile.scss

.section-profile-headings {
  padding: 15px 0 33px;
}

.profile-image {
  border: 2px solid $white;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
  max-height: 131px;
  max-width: 131px;
}

.profile-name {
  color: $purple;
  font-weight: $bold;
  margin-bottom: 0;
}

.kaplan-consultant-profiles-section {
  .section-purple {
    height: 105px ;
  }
  h5 {
    color: $white;
    margin-bottom: .4em;
    text-align: center;
  }
  ul {
    //font-size: em(14);
  }
}

.bottom-grid-cell {
  margin-top: -105px;
  &:last-child {
  @include breakpoint($small-screen-down) {
    margin-top: 0;
   }
  }
}

.kaplan-profiles {
  margin-top: -90px;
  text-align: center;
}

.bottom-grid {
  @include container;
  .bottom-grid-cell {
    float: left;
    width: 50%;
      &:first-child {
        border-right: 1px solid $base-border-color;
        //padding: 0 3em .5em 0;
      }
    @include breakpoint($small-screen-down) {
      width: 100%;
      &:first-child {
        border-right: none;
        padding: 0;
      }
    }
    > h5 {
      border-bottom: 1px solid $purple;
      padding-bottom: .5em;
    }
  }
}

.for-help-list {
  padding-left: 2em;
  margin: 0 auto;
  max-width: 20em;
}


// On individual profile pages

 .individual-profile {
   border-top: 125px solid $purple;
 }
