//pages/_faculty-dashboard

.dashboard-grid-container {
  @include container;
}

.dashboard-grid {
  background: #fff;
  border-top: 10px solid $teal;
  box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.13);
  height: 22.5em;
  overflow: hidden;
  padding: 1.4em 0;
  position: relative;
  margin-top: 1.5em;
  @include span(6);
//  @include omega(2n);
   @include breakpoint($medium-screen-down) {
     @include span(12);
     height: auto;
   }
   &:nth-child(2), &:nth-child(4) {
     @include last;
   }

   @media screen-down and (min-width: 678px) and (max-width: 768px) {
    &-wide {
    width: 48.8%;
    margin-right: 0;
    clear: none;
    height: 24em;
   }
    .text-larger-teal {
      font-size: 1em;
    }
 }

@media screen-down and (min-width: $medium-screen-up) and (max-width: $large-screen-down) {
 &.dashboard-grid--red {
   td {
     font-size: em(14);
   }
  }
 }

   table tr td {
    padding-right: 1.5em;
  }

  table tr td:last-child {
   padding-right: 1.5em;
   text-align: right;
 }
   table tr td:first-child {
      padding-left: 1.5em;
      text-align: left;
    }
    .indented {
      position: absolute;
      bottom: 1em;
      @include breakpoint($medium-screen-down) {
        position: static;
      }
    }

    h3 {
      padding-left: 1.1em;
    }
}

.breadcrumb {
  @include container {
  }
  @include breakpoint($small-screen-down) {
    text-align:center;
  }
    &--wide {
      @include span(8);
        @include breakpoint($small-screen-down) {
          @include span(4);
        }
    }
    &-narrow {
      @include span(4);
      float: right;
        @include breakpoint($small-screen-down) {
            float: none;
            width: 100%;
        }
      > .button {
        display: inline-block;
        float: right;
          @include breakpoint($small-screen-down) {
            float: none;
            margin-top: 1.5em;
            width: 100%;
          }
         a {
           color: #fff;
         }
      }
    }
}

.bar-chart {
  @include container();
  max-width: 55%;
  .bar, .below {
    color: $white;
    @include span(6);
    > .percent {
      text-align: center;
      font-weight: bold;
      font-size: 2em;
      display: inherit;
      padding-top: 1em;
    }
    > .below {
      @extend .percent;
      font-size: 1em;
      position: relative;
      top: 4em;
    }
  }

  .below {
    font-weight: 800;
    color: $teal;
    margin-top: .5em;
    margin-bottom: .5em;
    text-align: center;
     &:last-child {
       color: $purple;
     }
  }
   &-teal {
     background: $teal;
     height: 13em;
   }
   &-purple {
     background: $purple;
     height: 11em;
     position: relative;
     top: 2em;
   }
}

//Begin Faculty Links

.tools-link-grid {
  @include container;
  @include breakpoint($small-screen-down) {
      @include span(12);
      border-right: 0;
      text-align: center;
  }
  > h4 {
    margin-bottom: 0.4em;
  }
}

.reporting-tools-grid  {
  @include span(6);
  border-right: 2px solid $base-border-color;
  @include breakpoint($small-screen-down) {
      @include span(12);
      border-right: 0;
  }
}

.faculty-link-list {
  margin: 0 3em;
  border-bottom: 1px solid $base-border-color;
  padding: .5em;
  padding-bottom: 1.2em;
    &:not(:first-child) {
     padding-top: 1.3em;
    }
    &:last-child {
      border-bottom: none;
    }
    @include breakpoint($small-screen-down) {
      @include span(12);
      text-align: center;
      margin-top: 1em;
    }
    @include breakpoint($large-screen-down) {
      margin: 0 1em;
    }
    @include breakpoint($medium-screen-down) {
      margin: 0 1em 0 0;
      padding-left: 0;
    }
}

.teaching-tools-grid{
  @include span(last 6);
  @include breakpoint($small-screen-down) {
      @include span(12);
      border-right: 0;
      text-align: center;
  }
  .faculty-link-list {
    > span {
      color: $teal;
      font-size: em(14px);
    }
    .bullet {
      @include breakpoint($small-screen-down) {
       display: inline-block;
       width: auto;
     }
    }
  }
}

//End Faculty Links

.performance-snapshot {
  @include clearfix;
  margin: 0 1.5em 1em;
  &.border-bottom {
    border-bottom: 1px solid $base-border-color;
    padding-bottom: 1em;
  }
  > .text-larger-teal {
    padding-top: 1em;
    margin-bottom: .6em;
  }
}
