//modules/_radios.scss

//Large Custom Radios

.radio-large {
  input[type="radio"] {
    //makes default input invisible, but keeps it tab accessable
    @extend .screen-reader-only;
    &:checked +  label:before {
    @include retina-image(radio-checked, 30px 31px);
     content: " ";
    + input[type="radio"]:focus{
      outline: 1px solid black;
    }
   }
  }
  > label {
    display: inline-block;
    cursor: pointer;
    position: relative;
    padding-left: 45px;
    margin-right: 15px;
  }
  label:before {
   border-radius: 15px;
   content: "";
   display: inline-block;
   width: 30px;
   height: 30px;
   margin-right: 10px;
   position: absolute;
   left: 0;
   bottom: 4px;
   background-color: #fff;
  }
  > p {
    padding-left: 45px;
  }
}
