//layout/_container.scss

//  The .container class is applied to restrict the
//  max-width of an element, usually a bit of content.
//  It also includes right and left padding.

//  Top and bottom padding are applied with a .section element
//  from modules/_section.scss used as a wrapper div

//  Often, you will place a .container div within another
//  element that has a background color. This gives you
//  a full-bleed background, but the content is contained
//  within the max-width.

//  Example: Container within a section
//  <section class="awesome-section-with-background-color">
//    <div class="container">
//      <p>Your content goes here... </p>
//    </div>
//  </section>

//outer-container mixin from neat/grid/_outer-container.scss
//sets max-width, but does not include padding
.container-no-pad {
  @include container();
}
.container-medium-width-no-pad {
  @include container(970px);
}

$container__max-width: 70em;

//Set the default container max-width
$max-width: 74em;

