// pages/_sign-in.scss
//Sign In Page Styles

.sign-in {
  background: $dark-gray;
  color: white;
  padding: 1.5em 0 1.5em;
    @include breakpoint($small-screen-down) {
      padding-top: .5em;
    }
}

.radio-sign-in {
  @include clearfix;
  float: left;
  padding: 1.1em 1.5em;
  width: 50%;
  @include breakpoint($small-screen-down) {
    width: 100%;
    p {
      display: none;
    }
  }
  &-faculty {
    background: $teal;
  }
  &-student {
    background: $purple;
  }
}

%sign-in-grid {
  margin: 0 1.5em;
  @include breakpoint($small-screen-up) {
    max-width: 300px;
  }
  > .text-larger, .lead {
   margin-bottom: .25em;
 }
  @include breakpoint($small-screen-down) {
    > label {
      margin-bottom: 0;
    }
  }
}

.sign-in-faculty-grid {
  @extend %sign-in-grid;
  float: right;
    @include breakpoint($small-screen-down) {
      float: left;
    }
}

.sign-in-student-grid {
  @extend %sign-in-grid;
  float: left;
    @include breakpoint($medium-screen-down) {
      padding-right: 1em;
    }
}

//Forgot Password Link
.forgot-password {
  display: inline-block;
  padding: $base-spacing 0 0;
    @media (max-height: 800px) {
      padding-top: 1em;
    }
  &:hover, &:focus {
    color: tint($action-color, 40%);
  }
}
