//pages/edit-info-pages.scss

.edit-info > .button, .manage > .button {
  margin-top: 1em;
}

.active-cohorts {
  border-bottom: 1px solid $light-gray;
  margin-left: -1em;
  padding-left: 1em;
  padding-top: 1em;
  &:hover, &:focus {
    background-color: darken($base-background-color, 3%);
  }

   > label {
    min-width: 175px;
    width: 20%;
  }
}

.settings-column-left {
  display: inline;
  float: left;
  min-width: 175px;
  width: 20%;
}

input.page-lg-heading {
  font-size: em(28);
  padding: .4em;
}

.first-name-input {
  width: 40%;
  float: left;
  padding-right: 2em;
  @include breakpoint($small-screen-down) {
    width: 100%;
    padding-right: 0;
  }
}

.last-name-input {
  float: left;
  width: 60%;
  @include breakpoint($small-screen-down) {
    width: 100%;
  }
}
