// modules/_dropdown.scss

.dropdown {
  $dropdown-color: $base-font-color;
  $dropdown-description-color: lighten($dropdown-color, 40%);
  $dropdown-border: $base-border-darker;
  $dropdown-background: $base-background-color;
  $dropdown-background-hover: darken($dropdown-background, 3%);
  $dropdown-inner-border: $base-border-darker;
  $dropdown-height: 30px;
  $dropdown-padding: 1em;
  $dropdown-distance-from-menu: 2.5em;
  $dropdown-arrow-top-distance: 0;
  float: right;
  line-height: $base-line-height;
  @include breakpoint($medium-screen-down) {
    float: none;
    text-align: center;
  }

  .dropdown-container {
    position: relative;
    text-align: center;
    @include breakpoint($medium-screen-down) {
      border-top: 1px solid $medium-gray;
      display: inline-block;
      width: 100%;
    }
  }

  .dropdown-description {
    background: $dropdown-background;
    border-bottom-left-radius: $base-border-radius;
    border-bottom: $dropdown-border;
    border-left: $dropdown-border;
    border-top-left-radius: $base-border-radius;
    border-top: $dropdown-border;
    color: $dropdown-description-color;
    float: left;
    font-size: 0.7em;
    line-height: $dropdown-height;
    margin: 0;
    padding: 0 ($dropdown-padding / 2) 0 $dropdown-padding;

    @include breakpoint($large-screen-down) {
      font-size: 1em;
    }
  }

  .dropdown-button {
    @include retina-image(downwards-triangle, 12px 8px);
    background-position: right center;
    color: #fff;
    cursor: pointer;
    float: right;
    font-weight: $bold;
    line-height: $dropdown-height;
    margin: 0;
    padding: 0 ($dropdown-padding * 1.5) 0 ($dropdown-padding / 2);
    position: relative;
    @include breakpoint($medium-screen-down) {
      margin-right: 0;
    }
    @include breakpoint($medium-screen-down) {
      background-position: 95% center;
      display: block; //to allow icon to be positioned outside text when using <a> element for accessability
      float: none;
      font-size: 1.1em;
      line-height: $dropdown-height * 1.5;
      padding: 0;
    }
  }

  .dropdown-menu {
    @include transition (all 0.2s ease-in-out);
    background: #fff;
    border: $dropdown-border;
    box-shadow: 0 2px 2px transparentize(black, 0.8);
    color: $dropdown-color;
    cursor: pointer;
    display: none;
    margin: 0;
    overflow: show;
    padding:  1em 0;
    position: absolute;
    right: 0;
    top: $dropdown-distance-from-menu;
    z-index: 99999;
    @include breakpoint($small-screen-down) {
      padding: 0;
      position: static;
      width: 100%;
    }
  }

  .dropdown-menu li {
    color: $dropdown-color;
    font-weight: 800;
    list-style: none;
    text-align: left;
    white-space: nowrap;
      @include breakpoint($small-screen-down) {
        &:nth-child(even) {
          background-color: $dropdown-background-hover;
        }
      }
     a {
       color: $dropdown-color;
       display: block;
       padding: .5em 3em .5em 4.2em;
       @include breakpoint($small-screen-down) {
          padding: 1em 4em 1em 8em;
       }
       &:hover, &:focus {
        background: $dropdown-background-hover;
        @include breakpoint($small-screen-down) {
           background-color: darken($dropdown-background-hover, 5%);
        }
       }
      }

     &:first-child {
      border-top-left-radius: $base-border-radius;
      border-top-right-radius: $base-border-radius;
    }
     &:last-child {
      border: none;
      border-bottom-left-radius: $base-border-radius;
      border-bottom-right-radius: $base-border-radius;
    }
  }

  .show-menu {
    display: block;
  }
}

.open > .dropdown-menu {
  display:block;
}

// icons

.menu-icon:before {
  content:" ";
  display: block;
  position: absolute;
  left: 2em;
  @include breakpoint($small-screen-down) {
    left: 5em;
  }
}

.profile-icon:before {
  @include retina-image(profile-icon, 20px 20px);
  height: 20px;
  width: 20px;
}

.cohorts-icon:before {
  @include retina-image(my-cohorts-icon, 20px 20px);
  height: 20px;
  width: 20px;
}

.students-icon:before {
  @include retina-image(my-students-icon, 21px 18px);
  height: 18px;
  width: 21px;
}

.pencil-icon:before {
  @include retina-image(pencil-icon, 20px 20px);
  height: 20px;
  width: 20px;
}

.home-icon:before {
  @include retina-image(home-icon, 21px 20px);
  height: 21px;
  width: 20px;
}

.sign-out-icon:before {
  @include retina-image(sign-out-icon,20px 20px);
  height: 20px;
  width: 20px;
}
