//base_html.scss

//HTML should contain the site's default background color
//and font-size
//------------------------------------------------------

html {
  background: $white;
  font-size: $base-font-size;
  height: 100%;
  width: 100%;
    @include breakpoint($large-screen-down) {
      font-size: 96%;
    }
    @include breakpoint($small-screen-down) {
      font-size: 94%;
    }
}


html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }
